/*重写ivew导航栏的样式*/
#slidermenu .ivu-menu:after {
  width: 0 !important;
}
#slidermenu .ivu-menu-vertical .ivu-menu-item {
  padding: 10px 15px !important;
  background: #fff;
}
#slidermenu .ivu-menu-vertical .ivu-menu-item:hover, #slidermenu #slidermenu .ivu-menu-vertical .ivu-menu-submenu-title:hover {
  color: #515a6e;
  background: #54637f;
}
#slidermenu .ivu-menu-vertical .ivu-menu-item:hover a, #slidermenu #slidermenu .ivu-menu-vertical .ivu-menu-submenu-title:hover a {
  color: #fff !important;
}
#slidermenu .ivu-menu-submenu .ivu-menu-submenu-title {
  padding: 5px 15px !important;
  display: none;
}
#slidermenu .ivu-menu-opened .ivu-menu-submenu-title {
  color: #fff !important;
  background: #54637f;
}
#slidermenu .ivu-menu-submenu .ivu-menu-item-selected:hover a {
  color: inherit !important;
}
#slidermenu .ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu) {
  background: #E2E2E2 !important;
}
#slidermenu .ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu):after {
  width: 0 !important;
}
#slidermenu .ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu):before {
  content: "";
  display: block;
  width: 8px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  background: #54637f;
  border-right: #ECECEC 2px solid;
  -webkit-box-shadow: 2px 0 0 0 #54637f;
          box-shadow: 2px 0 0 0 #54637f;
}

/*重写ivew角色添加弹窗的样式*/
#modal_role_add .ivu-modal-wrap {
  width: 100%;
  height: 100%;
}

